import {paymentServiceGetRequest, paymentServiceRequest} from '../../payment-service-request';
import {config} from '../config';

export function runGetSchemasHandleResponse(apiKey, refundToken) {
  return paymentServiceGetRequest(apiKey, `${config.paymentServiceBaseUrl}/refund-schemas/${refundToken}`)
    .then((response) => {
      return response.data.refundSchemaItems;
    })
    .catch(() => {
      return [
      ];
    });
}

export function runPostSchemasHandleResponse(apiKey, refundToken, data) {
  return paymentServiceRequest(data, apiKey, `${config.paymentServiceBaseUrl}/refund-schemas/${refundToken}`)
    .then((response) => {
      return response.data;
    });
}